"use client";
import { useEffect, useState } from "react";
import SkillBar from "../components/SkillBar";
import AttentionScatter from "../components/AttentionScatter";
import StudentRadar from "../components/StudentRadar";

export default function Home() {
  const [students, setStudents] = useState([]);
  const [selected, setSelected] = useState(null);

  useEffect(() => {
    fetch("/data.json")
      .then(res => res.json())
      .then(setStudents);
  }, []);

  if (students.length === 0) return <p>Loading...</p>;

  // Overview stats
  const avgScore = (students.reduce((a, b) => a + b.assessment_score, 0) / students.length).toFixed(1);
  const topClass = [...new Set(students.map(s => s.class))].map(cls => {
    const scores = students.filter(s => s.class === cls).map(s => s.assessment_score);
    return { cls, avg: scores.reduce((a, b) => a + b, 0) / scores.length };
  }).sort((a, b) => b.avg - a.avg)[0].cls;

  return (
    <div style={{ padding: 20 }}>
      <h1>Student Performance Dashboard</h1>

      <div style={{ display: "flex", gap: 20 }}>
        <div>Total Students: {students.length}</div>
        <div>Average Score: {avgScore}</div>
        <div>Top Class: {topClass}</div>
      </div>

      <h2>Average Score by Class</h2>
      <SkillBar data={students} />

      <h2>Attention vs Score</h2>
      <AttentionScatter data={students} />

      <h2>Student Profile (Radar)</h2>
      <button onClick={() => setSelected(students[0])}>Show Student 1</button>
      <StudentRadar student={selected} />
    </div>
  );
}
